import {
	JUMOO_PROCESSING_CONTEXT,
	PipelineContext,
	ProcessStepElement,
	ProcessStepElementBase,
} from '@jumoo/processing';
import { css, customElement, html } from '@umbraco-cms/backoffice/external/lit';
import { uSyncSnapshots } from '../../constants';
import { SnapshotsProcessingOptions } from '../../api';

@customElement(uSyncSnapshots.steps.createConfig)
export class uSyncSnapshotsCreateConfigElement
	extends ProcessStepElementBase
	implements ProcessStepElement
{
	#pipelineContext?: PipelineContext;

	constructor() {
		super();

		this.consumeContext(JUMOO_PROCESSING_CONTEXT, (context) => {
			this.#pipelineContext = context;
		});
	}

	#onNameChange(e: CustomEvent) {
		if (!this.options) return;

		const element = e.target as HTMLInputElement;
		if (!element) return;
		this.#pipelineContext?.setPending({ name: element.value });
	}

	#onIncludeFiles(e: CustomEvent) {
		if (!this.options) return;

		const element = e.target as HTMLInputElement;
		if (!element) return;
		this.#pipelineContext?.setPending({ includeFiles: element.checked });
	}

	#onIncludeMedia(e: CustomEvent) {
		if (!this.options) return;

		const element = e.target as HTMLInputElement;
		if (!element) return;
		this.#pipelineContext?.setPending({ includeMedia: element.checked });
	}

	render() {
		var options = this.options as SnapshotsProcessingOptions;
		if (!options) return;

		return html` <uui-box .headline=${this.localize.term('uSyncSnapshots_create')}>
			<umb-localize key="uSyncSnapshots_createSummary"></umb-localize>
			<umb-property-layout
				.label=${this.localize.term('uSyncSnapshots_createName')}
				.description=${this.localize.term('uSyncSnapshots_createNameDescription')}>
				<div slot="editor">
					<uui-input
						type="text"
						label="Name"
						.value=${options.name}
						@change=${this.#onNameChange}></uui-input>
				</div>
			</umb-property-layout>

			<umb-property-layout
				.label=${this.localize.term('uSyncSnapshots_createFiles')}
				.description=${this.localize.term('uSyncSnapshots_createFilesDescription')}>
				<div slot="editor">
					<uui-checkbox
						label="Include files"
						.checked=${options.includeFiles}
						@change=${this.#onIncludeFiles}>
						<div slot="label"></div>
					</uui-checkbox>
				</div>
			</umb-property-layout>

			<umb-property-layout
				.label=${this.localize.term('uSyncSnapshots_createMedia')}
				.description=${this.localize.term('uSyncSnapshots_createMediaDescription')}>
				<div slot="editor">
					<uui-checkbox
						label="Include media"
						.checked=${options.includeMedia}
						@change=${this.#onIncludeMedia}>
						<div slot="label"></div>
					</uui-checkbox>
				</div>
			</umb-property-layout>
		</uui-box>`;
	}

	static styles = css`
		uui-input {
			width: 100%;
		}
	`;
}

export default uSyncSnapshotsCreateConfigElement;
